// 
let lyex = {
    store: {
        position: '',
        skill: ['jsx', 'ts'],
        name: "lllll",
        show:"",
        activeChange:"",
        toggleChildren:"",
        togglehome:"",
        toggleheader:"",
        send:""
    },
    // 
    list: {},
    // 
    // 
    on(key, fn) {
        // 
        if (!this.list[key]) {
            // 
            this.list[key] = [];
        }
        this.list[key].push(fn);
    },
    // 
    emit(key, param) {
        // 
        let fns = this.list[key];
        // 
        for (let p in param) {
            this.store[p] = param[p]
        }
        // 
        fns.forEach(fn => {
            // 
            fn(param);
        });
    }
}

export default lyex